  .grid__cell {
    width: calc(100% / var(--columns) * var(--cols));
    padding-right: calc(var(--column-gap) / 2);
    padding-left: calc(var(--column-gap) / 2);
    padding-top: calc(var(--row-gap) / 2);
    padding-bottom: calc(var(--row-gap) / 2);
